.Select-menu-outer {
  max-height: 250px !important;
}
.Select-menu-outer > div {
  max-height: 200px !important;
}
.input-link {
  font-size: 12px;
  color: cornflowerblue;
  display: block;
  text-align: right;
  padding-top: 5px;
  margin-right: 30px;
}
.box-wrapper {
  display: block;
  /* padding-left: 10px;
  padding-right: 25px; */
}
.half {
  width: 50%;
  float: left;
  /* margin-left: 5%; */
}
.success-message {
  display: block;
  text-align: center;
  padding-top: 100px;
  background: url("../images/checked.svg");
  background-size: 64px 64px;
  background-position: top center;
  background-repeat: no-repeat;
  font-weight: 500;
  font-size: 18px;
  color: #8bc34a;
  margin-top: 30px;
  animation-name: zoomIn;
  animation-duration: 1s;
  letter-spacing: 1px;
}
.bottom {
  position: absolute;
  bottom: 0px;
  display: block;
}
.small-print {
  font-size: 11px;
  color: #666;
}
.small-print a {
  color: blue;
  text-decoration: underline;
}
@keyframes zoomIn {
  0% {
    background-size: 64px 64px;
  }
  25% {
    background-size: 74px 64px;
  }
  50% {
    background-size: 84px 84px;
  }
  100% {
    background-size: 74px 64px;
  }
}



